import { graphql, useStaticQuery } from 'gatsby';
import { Magazine } from 'src/models';

interface MagazinesResult {
  allMdx: {
    edges: Array<{ node: Magazine }>;
  };
}
export function useLubyconMagazines() {
  const result = useStaticQuery<MagazinesResult>(graphql`
    query {
      allMdx(filter: { frontmatter: { draft: { ne: true } } }) {
        edges {
          node {
            id
            fields {
              path
            }
            frontmatter {
              date(formatString: "MMMM DD, YYYY")
              title
              thumbnail {
                childImageSharp {
                  fixed(width: 640, height: 640, quality: 100, cropFocus: CENTER) {
                    ...GatsbyImageSharpFixed
                  }
                }
              }
            }
          }
        }
      }
    }
  `);

  return result.allMdx.edges.map(mdx => mdx.node);
}
