import { useMemo } from 'react';
import { BlogFeed, Member, RawBlog } from 'src/models';
import { useAllMembers } from 'src/hooks/useAllMembers';
import { graphql, useStaticQuery } from 'gatsby';

const convertRawBlogToBlog = (
  posts: RawBlog,
  members: Member[],
  memberId: 'Evan' | 'Ryan' | 'Jim' | 'MartinK' | 'MartinH'
) => {
  return posts.edges.map(post => ({
    ...post.node,
    author: members.find(member => member.id === memberId),
  }));
};

export function useBlogFeeds(): BlogFeed[] {
  const { allFeedEvanBlog, allFeedJimBlog, allFeedMartinHBlog, allFeedMartinKBlog } = useStaticQuery<{
    allFeedEvanBlog: RawBlog;
    allFeedMartinHBlog: RawBlog;
    allFeedMartinKBlog: RawBlog;
    allFeedJimBlog: RawBlog;
  }>(graphql`
    query RawBlogQuery {
      allFeedEvanBlog {
        edges {
          node {
            contentSnippet
            link
            title
            pubDate
          }
        }
      }
      allFeedJimBlog {
        edges {
          node {
            contentSnippet
            link
            title
            pubDate
          }
        }
      }
      allFeedMartinHBlog {
        edges {
          node {
            contentSnippet
            link
            title
            pubDate
          }
        }
      }
      allFeedMartinKBlog {
        edges {
          node {
            contentSnippet
            link
            title
            pubDate
          }
        }
      }
    }
  `);

  const members = useAllMembers();
  const blogs = useMemo(() => {
    return [
      ...convertRawBlogToBlog(allFeedEvanBlog, members, 'Evan'),
      ...convertRawBlogToBlog(allFeedMartinHBlog, members, 'MartinH'),
      ...convertRawBlogToBlog(allFeedMartinKBlog, members, 'MartinK'),
      ...convertRawBlogToBlog(allFeedJimBlog, members, 'Jim'),
    ];
  }, [allFeedEvanBlog, allFeedJimBlog, allFeedMartinHBlog, allFeedMartinKBlog, members]);

  return blogs;
}
