import { FluidObject, FixedObject } from 'gatsby-image';

export interface ImageSharp {
  childImageSharp: {
    fixed?: FixedObject;
    fluid?: FluidObject;
  };
}

export interface Menu {
  name: string;
  link: string;
  emoji?: string;
}

export interface MainContent {
  title: string;
  description: string;
  menu: Menu;
  buttonTitle: string;
  photo: string;
}

export interface Tool {
  name: string;
  url: string;
  icon?: ImageSharp;
  description: string;
  dri?: string;
}

export interface DRI {
  name: string;
  description: string;
  members: string[];
}

export * from './post';
export * from './members';
export * from './site';
export * from './auth';
