import { ImageSharp } from 'src/models';
import { Member } from './members';

interface Frontmatter {
  date?: string;
  title: string;
  thumbnail?: ImageSharp;
  draft?: boolean;
}

/**
 * @description 루비콘에서 작성한 매거진
 */
export interface Magazine {
  id: number;
  fields?: {
    path?: string;
  };
  body: string;
  frontmatter: Frontmatter;
  excerpt?: string;
}

export interface RawBlog {
  edges: Array<{
    node: {
      contentSnippet: string;
      link: string;
      title: string;
      pubDate: string;
    };
  }>;
}
/**
 * @description 멤버들의 블로그 피드
 */
export interface BlogFeed {
  author: Member;
  title: string;
  contentSnippet: string;
  pubDate: string;
  link: string;
}

export enum PostType {
  루비콘매거진 = 'INTERNAL',
  멤버블로그피드 = 'EXTERNAL',
}
/**
 * @description 매거진 & 블로그 피드의 추상화 모델
 */
export interface Post {
  author: {
    name: string;
    profileImage: string;
  };
  createdAt: string;
  type: PostType;
  link: string;
  title: string;
  excerpt: string;
}
