import React, { useMemo } from 'react';
import formatDistanceToNow from 'date-fns/formatDistanceToNow';
import koLocale from 'date-fns/locale/ko';
import { Card, CardHeader, CardContent, Avatar, Typography, makeStyles, CardActionArea } from '@material-ui/core';
import AnimatedListItem from 'components/Animated/AnimatedListItem';
import { Post } from 'src/models';

const useStyles = makeStyles({
  title: {
    fontWeight: 'bold',
  },
  link: {
    textDecoration: 'none',
  },
});

interface Props {
  post: Post;
}
const PostCard = ({ post: { createdAt, link, author, title, excerpt } }: Props) => {
  const publishedDate = useMemo(() => {
    return formatDistanceToNow(new Date(createdAt), {
      addSuffix: true,
      locale: koLocale,
    });
  }, [createdAt]);

  const style = useStyles();

  return (
    <AnimatedListItem>
      <a href={link} target="_blank" rel="noopener noreferrer" className={style.link}>
        <Card>
          <CardActionArea>
            <CardHeader
              avatar={<Avatar src={author.profileImage} alt={author.name} />}
              title={author.name}
              subheader={publishedDate}
            />
            <CardContent>
              <Typography gutterBottom={true} variant="subtitle1" component="h3" className={style.title}>
                {title}
              </Typography>
              <Typography variant="caption">{excerpt}</Typography>
            </CardContent>
          </CardActionArea>
        </Card>
      </a>
    </AnimatedListItem>
  );
};

export default PostCard;
