import { useEffect, useState } from 'react';
import { BlogFeed, Magazine, Post, PostType } from 'src/models';
import { getLogoSrc } from 'src/utils/logo';
import { useBlogFeeds } from './useBlogFeeds';
import { useLubyconMagazines } from './useLubyconMagazines';

function convertMagazineToPost(magazine: Magazine): Post {
  return {
    title: magazine.frontmatter.title,
    createdAt: magazine.frontmatter.date,
    excerpt: '',
    type: PostType.루비콘매거진,
    link: magazine.fields.path,
    author: {
      name: 'Lubycon',
      profileImage: getLogoSrc('light', true),
    },
  };
}

function convertBlogFeedToPost(feed: BlogFeed): Post {
  return {
    title: feed.title,
    createdAt: feed.pubDate,
    excerpt: `${feed.contentSnippet.substring(0, 100)}...`,
    type: PostType.멤버블로그피드,
    link: feed.link,
    author: {
      name: feed.author.name.en,
      profileImage: feed.author.profileImg.childImageSharp.fixed.src,
    },
  };
}

export function useAllPosts() {
  const magazines = useLubyconMagazines();
  const feeds = useBlogFeeds();
  const [posts, setPosts] = useState<Post[]>(undefined);

  useEffect(() => {
    const postFromMagazine = magazines.map(convertMagazineToPost);
    const postFromFeeds = feeds.map(convertBlogFeedToPost);
    const posts = [...postFromMagazine, ...postFromFeeds].sort(
      (a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime()
    );

    setPosts(posts);
    // eslint-disable-next-line
  }, []);

  return posts ?? [];
}
